}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

:root {
  --header-bg-color: #fff;
  --main-bg-color: #f6f6f6;
  --text-dark: #111;
  --text-light: rgba(17, 17, 17, .6);
  --active-category-color: #4f2ee8;
  --btn-color: #eac645;
  --light-grey: #b4afaf;
  --light-color: #e0e0e0;
  --black-color: #000;
  --cubic-bezier: cubic-bezier(.4, 0, .2, 1);
  --border: #111;
  --white-color: #fff;
  --violet-color: #4f2ee8;
}

.light-mode.night, .container-header.night {
  --header-bg-color: #111;
  --main-bg-color: #202024;
  --text-dark: #f3f3f3;
  --text-light: rgba(255, 255, 255, .6);
  --active-category-color: #eac645;
  --cubic-bezier: var(--cubic-bezier);
  --border: #fff;
  background-color: #111;
}

@media (prefers-color-scheme: dark) {
  :root:not([theme="light"]) {
    --header-bg-color: #111;
    --main-bg-color: #202024;
    --text-dark: #f3f3f3;
    --text-light: rgba(255, 255, 255, .6);
    --active-category-color: #eac645;
    --cubic-bezier: var(--cubic-bezier);
  }
}

