.title-best-sellers,
.title-category {

    font-size: 32px;
    line-height: 1.19;
    color: var(--text-dark);
    letter-spacing: -0.04em;
    text-transform: capitalize;
    margin-bottom: 40px;
}

.title-best-sellers-color,
.second-category-word {
    color: var(--active-category-color);
}



.item-category {
    font-weight: 400;
    font-size: 14px;
    line-height: 1.29px;
    color: var(--light-grey);
    letter-spacing: 0.03em;

    margin-bottom: 18px;
    display: block;
}


.img-book {
    transition: transform 350ms ease-in-out, filter 350ms ease-in-out;
    border-radius: 8px;
}

.img-book:hover {

    transform: scale(1.05);
}





.book-card {
    color: var(--text-dark);
    font-size: 14px;
}


.title-book {
    font-weight: 700;
    font-size: 16px;
    line-height: 1.12;
    letter-spacing: -0.04em;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
    color: var(--text-dark);
    margin-bottom: 4px;
}

.author-book {

    font-weight: 400;
    font-size: 12px;
    line-height: 1.17px;
    letter-spacing: -0.04em;
    color: var(--light-grey);
    padding-top: 4px;
    padding-bottom: 4px;
}


.author-book-category {

    font-size: 12px;
    line-height: 1.17px;
    color: var(--text-dark);
    letter-spacing: -0.04em;

}

.book-info {
    padding-top: 14px;
    padding-bottom: 4px;
}

.book-info-category {
    margin-bottom: 40px;
}





.see-more {
    font-family: inherit;
    font-weight: 700;
    font-size: 14px;
    line-height: 1.29;
    text-transform: uppercase;
    color: var(--text-dark);
    box-sizing: border-box;
    width: 124px;
    height: 46px;
    margin-left: auto;
    background-color: var(--main-bg-color);
    border: 2px solid var(--active-category-color);
    border-radius: 40px;

}

.see-more:hover,
.see-more:focus {
    background: var(--active-category-color);
    color: var(--header-bg-color);
}






.book-gallery {
    display: flex;
    flex-direction: column;
    gap: 40px;
}


// ===================== MODAL =====================================

@media screen and (min-width: 375px) {

    .book-container {
        width: 100%;
        padding-left: 20px;
        padding-right: 20px;
    }
}

@media screen and (max-width: 374.98px) {
    .img-card-book {
        width: 100%;
        height: 485px;
        margin-bottom: 14px;
    }
}

// ===================== TABLET ==================================

@media screen and (min-width: 768px) {

    .book-container {

        padding-left: 32px;
        padding-right: 32px;
    }


    .title-best-sellers,
    .title-category {
        white-space: nowrap;
        font-size: 48px;
        line-height: 1.08;



    }

    .img-card-book {
        width: 218px;
        height: 316px;
    }

    .book-info {

        width: 218px;

        margin-bottom: 20px;
    }

    .box-category {
        display: flex;
        gap: 25px;
        justify-content: space-between;
    }

    .book-gallery {
        flex-direction: row;
        flex-wrap: wrap;
        gap: 25px;
        row-gap: 40px;
        justify-content: space-between;
    }
}


@media screen and (min-width: 1439px) {

    .book-container {
        margin-top: 100px;

    }
}


