// ===========================  SCROLL ==============================

.scroll-to-top {
    display: none;
    border-radius: 50%;
    width: 54px;
    height: 54px;
    background-color: var(--active-category-color);
    border: none;
    position: fixed;
    bottom: 30px;
    right: 30px;
    animation-name: spin;
    animation-duration: 1s;
    animation-timing-function: linear;
    animation-iteration-count: infinite;
}

.scroll-icon {
    bottom: 10px;
    top: 10px;
    width: 20px;
    height: 20px;
}

@media screen and (max-width: 767px) {
    .scroll-to-top {
        display: none;
        border-radius: 50%;
        width: 40px;
        height: 40px;
        background-color: var(--active-category-color);
        border: none;
        position: fixed;
        bottom: 30px;
        right: 30px;
    }

    .scroll-icon {
        bottom: 10px;
        top: 10px;
        width: 12px;
        height: 12px;
    }
}

@media screen and (min-width: 768px) {
    .scroll-to-top {
        width: 64px;
        height: 64px;
        animation-name: spin;
        // animation-duration: 500ms;
        animation-timing-function: cubic-bezier(.29, .74, .29, .77);
        animation-iteration-count: infinite;
    }


    .scroll-icon {
        width: 20px;
        height: 20px;
    }
}


